import {SendSmsCode} from 'api/user'
export default{
	name:'UserRegister',
	data(){
		return {
			/*手机号*/
			mobile:'',
			/*下一步*/
			nextStatus:false,
			/*发送验证码*/
			sandStatus:false,
			/*倒计时*/
			second:0,
			/*验证码*/
			code:'',
			/*输入验证码*/
			smsCode:'',
		}
	},
	watch:{
		'smsCode'(value){
			if(value == this.code){
				this.nextStatus = true
			}
		}
	},
	methods:{
		/*发送二维码*/
		sendCode(){
			if(!this.sandStatus){
				SendSmsCode({
					mobile:this.mobile
				}).then(res=>{
					this.code = res.code
					if(res.result){
						uni.showToast({
							icon:'success',
							title: '发送成功'
						})
						this.sandStatus = true
						this.second = res.seconds
						setInterval(()=>{
							if(this.second > 0){
								this.second--
							}else{
								this.sandStatus = false
								clearInterval()
							}
						},1000)
					}else{
						uni.showToast({
							title: res.msg,
							icon:'none'
						})
					}
				})	
			}
			
		},
		/*下一步*/
		next(url){
			if(this.nextStatus){
				uni.navigateTo({
				    url: url
				});
			}
		}
	}
}
